// Social styles from â€”> https://socialsharepreview.com/
@import '../../../../assets/styles/theme/config';

// Preview
.preview {
  background-color: white;
  border-top: 1px solid $colorElementMid;

  &.active {
    border: none;
  }

  .content {
    padding: $spacingMedium;
    border: 1px solid #d3dce0;
    min-height: 390px;
  }
}

.arrowDown {
  position: relative;
  display: inline-block;
  width: 8px;
  height: 5px;
  vertical-align: middle;
  margin: 0 5px;

  .icon {
    top: 50%;
    left: 50%;
    transform: translateY(-50%) translateX(-50%);
    position: absolute;
    border-style: solid;
    border-width: 5px 4px 0 4px;
    width: 0;
    height: 0;
    border-color: #3c4043 transparent;
  }
}
.post {
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif !important;
  color: #38363b;
  max-width: 520px;

  .thumbnailWrapper {
    position: relative;
    background-color: #1d2129;
  }

  .thumbnail {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }

  .text {
    display: block;
    width: 100%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    word-break: break-word;
    vertical-align: middle;
  }
}

// Google
.post.post--google {
  font-family: arial, sans-serif;
  font-size: 14px;
  background-color: white;
  box-shadow: 0 0 10px 0px rgba(0, 0, 0, 0.08);
  max-width: 600px;

  .captionWrapper {
    padding: $spacingMedium;
  }

  .text {
    white-space: normal;
    overflow: visible;
    text-overflow: clip;
    word-break: break-word;

    &.title {
      font-size: 20px;
      line-height: 24px;
      color: #1a0dab;
      margin-bottom: 3px;
      padding-top: 4px;
    }

    &.description {
      font-size: 14px;
      line-height: 1.58;
      margin-bottom: 3px;
    }

    &.url {
      font-size: 14px;
      line-height: 1.3;
      color: #3c4043;
    }
  }
}

// Twitter
.post.post--twitter {
  line-height: 1.3em;
  font-size: 14px;
  background-color: #ffffff;
  border: 1px solid #bec9d0;
  overflow: hidden;
  border-radius: 0.42857em;

  .thumbnailWrapper {
    padding-bottom: calc(1 / 2 * 100%);
  }

  .captionWrapper {
    padding: 0.75em 1em;
  }

  .caption {
    max-height: 60px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: space-between;
  }

  .text {
    &.url {
      color: #899aa6;
      font-size: 14px;
      line-height: 18.2px;
      text-transform: uppercase;
    }

    &.title {
      font-weight: bold;
      margin: 0 0 0.15em;
      font-size: 14px;
      line-height: 18.2px;
      color: #000;
    }

    &.description {
      margin: 0 0 0.15em;
      max-height: 36px;
      overflow: hidden;
      font-size: 14px;
      line-height: 18.2px;
      color: #000;
    }
  }
}

// Facebook
.post.post--facebook {
  line-height: 16.8px;
  font-size: 14px;
  background-color: #f2f3f5;
  border: 1px solid #d3d3d3;

  .thumbnailWrapper {
    padding-bottom: calc(630 / 1200 * 100%);
  }

  .captionWrapper {
    padding: 10px 12px;
  }

  .caption {
    max-height: 60px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: space-between;
  }

  .text {
    &.url {
      text-transform: uppercase;
      color: #606770;
      font-size: 12px;
      line-height: 16px;
      margin-bottom: 2px;
    }

    &.title {
      font-weight: bold;
      margin: 0 0 0.15em;
      color: #1d2129;
      font-size: 16px;
      line-height: 20px;
    }

    &.description {
      color: #606770;
      font-size: 14px;
      line-height: 20px;
    }
  }
}
