.title {
  margin: 0;
  font-size: 1rem;
  line-height: 1.5;
  word-break: break-word;
  text-overflow: ellipsis;
  overflow: hidden;
  color: $colorTextDark;
  font-weight: 700;
}

.heading {
  margin-bottom: $spacingExtraSmall;
  font-size: 0.75rem;
  letter-spacing: 0.1rem;
  text-transform: uppercase;
  font-weight: 700;
  color: $colorTextDark;
}

.input {
  outline: none;
  background-color: #fff;
  border: 1px solid #d3dce0;
  max-height: 2.5rem;
  color: $colorTextMid;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Helvetica, Arial,
    sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol;
  font-size: 0.875rem;
  padding: 0.65625rem;
  margin: 0;
  width: 100%;
  -webkit-appearance: textfield;
}

.button {
  cursor: pointer;
  box-sizing: border-box;
  height: 2.5rem;
  display: inline-block;
  padding: 0;
  border: 0.0625rem solid #c3cfd5;
  border-radius: 0.125rem;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Helvetica, Arial,
    sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol;
  font-size: 0.875rem;
  overflow: hidden;
  background-size: 100% 200%;
  background-color: transparent;
  -webkit-transition: background 0.2s ease-in-out, opacity 0.2s ease-in-out,
    border-color 0.2s ease-in-out;
  transition: background 0.2s ease-in-out, opacity 0.2s ease-in-out,
    border-color 0.2s ease-in-out;
  vertical-align: middle;
  text-decoration: none;

  &.primary {
    border-color: $colorPrimaryDark;
    background-image: -webkit-gradient(
      linear,
      left bottom,
      left top,
      from($colorPrimary),
      to($colorPrimaryLight)
    );
    background-image: linear-gradient(0deg, $colorPrimary, $colorPrimaryLight);
    background-color: var(---color-blue-base);
    background-size: 100% 200%;

    .label {
      color: $colorElementLightest;
    }
  }

  &.naked {
    background-color: transparent;
    border: none;

    .label {
      color: $colorLink;
    }
  }

  &.submit {
    margin-top: $spacingSmall;
  }

  &.action {
    border: none;
    margin-top: 0;
    margin-left: 0;
    margin-right: 0;
    font-size: 24px;

    &:hover {
      background-color: $colorElementLight;
    }

    .innerWrapper {
      padding: $spacingExtraSmall;
    }

    .label {
      margin: 0;
      line-height: 1;
    }
  }

  .innerWrapper {
    display: flex;
    z-index: 1;
    padding: 0 0.875rem;
    align-items: center;
    justify-content: center;
  }

  .label {
    margin: 0 0.25rem;
    color: $colorTextMid;
    line-height: 2;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
  }

  .icon {
    display: block;
  }
}

.textLink {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  border: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Helvetica, Arial,
    sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol;
  font-size: 0.875rem;
  text-decoration: underline;
  background: none;
  transition: color 0.1s ease-in-out;
  appearance: none;
  white-space: normal;
  text-align: left;

  &.primary {
    color: $colorLink;

    &:hover {
      color: $colorTextDark;
      cursor: pointer;

      .icon {
        fill: $colorTextDark;
      }
    }
  }

  &.spacing {
    padding: $spacingSmall 0;
  }

  .icon {
    fill: $colorLink;
    padding-right: 4px;
  }
}

.card {
  cursor: pointer;
  display: flex;
  height: 48px;
  width: 100%;
  align-items: stretch;
  border: 1px solid $colorElementMid;
  border-radius: 2px;
  background-color: white;
  box-shadow: $boxShadowDefault;
  margin-bottom: $spacingSmall;

  &.single {
    margin-bottom: 0;
  }

  &:last-of-type {
    margin-bottom: 0;
  }

  &:hover {
    border-color: $colorPrimary;
  }

  .handle {
    cursor: grab;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: $colorElementLightest;
    border-right: 1px solid $colorElementMid;
    width: 1.25rem;
    height: 100%;

    &:hover {
      background-color: $colorElementLight;
    }
  }

  .wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: $spacingSmall;
    padding-right: $spacingSmall;
    width: 100%;
  }

  .row {
    display: flex;
    align-items: center;
    margin: 0 -4px;
  }

  .column {
    padding: 0 4px;

    .button {
      &:last-of-type {
        margin-right: -4px;
      }
    }
  }

  .description {
    color: $colorTextLight;
  }
}

.tabs {
  display: flex;
  background-color: white;

  &.border {
    border-left: 1px solid $colorElementMid;
    border-right: 1px solid $colorElementMid;
  }

  .tab {
    border: none;
    background: none;
    box-shadow: none;
    min-width: 100px;
    white-space: nowrap;
    color: #2a3039;
    position: relative;
    cursor: pointer;
    padding: 0 0.75rem;
    height: 56px;
    line-height: 56px;
    font-size: 0.875rem;
    font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Helvetica, Arial,
      sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol;
    font-weight: 400;
    outline: none;
    text-decoration: none;

    &.active {
      border-bottom: $colorPrimary solid 3px;
      background-color: white;
    }
  }
}
